import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/config/app.constant';
// import { any } from 'src/app/shared/models/user';
// import  } from 'src/app/shared/models/login';
// import { any } from '../security-policy/security-policy/security-dto';
import { BehaviorSubject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class LoginService {
  private uri = AppConstants.baseURL;
  dashboardapi =  AppConstants.baseURL + '/dashboard';
  userapi= AppConstants.baseURL + '/users';
  public departmentname:BehaviorSubject<any> = new BehaviorSubject<any>('');
  public logouttime:BehaviorSubject<any> = new BehaviorSubject<any>('');

  constructor(private http: HttpClient) { }
  getOtp(login) {
    return this.http.post<any>(`${this.uri}/login/otp`, login);
  }
  loginService(login) {
    return this.http.post<any>(`${this.uri}/login/credentials`, login);
  }
  forgetPassword(login) {
    return this.http.post<any>(`${this.uri}/login/forgetPsw`, login);
  }
  fristTimePassword(login) {
    return this.http.post<any>(`${this.uri}/user/passwordReset`, login);
  }
  getOtpForReset(login) {
    return this.http.post<any>(`${this.uri}/users/otpForReset`, login);
  }
  userPasswordExpireNotif(user_id) {
    return this.http.get(`${this.uri}/login/pwdNotif/${user_id}`);
  }
   
  fetchSecurityPolicyService() {
    return this.http.get<any>(`${this.uri}/securityPolicy/fetch`);
  }
  fetchMedUser(userId) {
    return this.http.get<any>(`${this.uri}/login/getUserByUserId/${userId}`);
  }

  settingdepartmentlogged(department, lastloggedin)
  {
     this.departmentname.next(department);
     this.logouttime.next(lastloggedin);
  }

  gettingloginTimeandDepartment(userName)
  {
    return this.http.get<any>(`${this.userapi}/loganddept/${userName}`)
  }
}
